import React from "react";
import "./InputGroup.scss";

const InputGroup = ({ name, title, type, onChange, value }) => {
  return (
    <div className={`input-group ${name}`}>
      <label htmlFor={name}>{title}</label>
      <input
        type={type}
        name={name}
        id={name}
        onChange={onChange}
        value={value}
      />
    </div>
  );
};

export default InputGroup;
