import React from "react";
import "./BookGroup.scss";

const BookGroup = ({image, title}) => {
  return (
    <div className="book-group">
      <div className="book-image">
        <img src={image} alt="book" />
      </div>
      <div className="book-detail">
        <div className="book-name">
          <h4>{title}</h4>
        </div>
        <div className="book-controls">
          <button className="book-links">View</button>
          <button className="book-links">Edit</button>
          <button className="book-links">Delete</button>
        </div>
      </div>
    </div>
  );
};

BookGroup.defaultProps = {
  image: "http://covers.openlibrary.org/b/id/240727-M.jpg",
  title: "Birds of Prey"
};

export default BookGroup;
