import React from "react";
import "./Dashboard.scss";
import Card from "../../../components/Card/Card";
import DashCards from "./DashCards/DashCards";

const Dashboard = ({
  firstName,
  lastName,
  totalBooks,
  lentBooks,
  availableBooks
}) => {
  return (
    <div className="dashboard">
      <Card className="dashboard-name">
        <h2>
          Welcome Back! {firstName} {lastName}
        </h2>
      </Card>
      <Card>
        <p>You hava a total number of {totalBooks} books in the library.</p>
      </Card>
      <Card>
        <p>You have lent out {lentBooks} books.</p>
      </Card>
      <Card>
        <p>You have {availableBooks} available books.</p>
      </Card>
      <DashCards description="Add Book" className="dashlinks" />
      <DashCards
        description="All Books"
        place="/dashboard/getbook"
        className="dashlinks"
      />
    </div>
  );
};

Dashboard.defaultProps = {
  firstName: "Victor",
  lastName: "Bassey",
  totalBooks: 37,
  lentBooks: 5,
  availableBooks: 32
};
export default Dashboard;
