import React from "react";
import "./SideBar.scss";
import SideBarLink from "./SidebarLink/SideBarLink";

const navlinks = [
  {
    name: "Dashboard",
    link: "/dashboard"
  },
  {
    name: "Get A Book",
    link: "/dashboard/getbook"
  }
];

const SideBar = () => {
  return (
    <ul className="sidebar">
      <div className="logo">
        <h2>GIDI LIBRARY</h2>
      </div>
      {navlinks.map(nav => (
        <SideBarLink key={nav.name} path={nav.link}>
          {nav.name}
        </SideBarLink>
      ))}
    </ul>
  );
};

export default SideBar;
