import React, { useState } from "react";
import "./GetBooks.scss";
import BookGroup from "../../../components/BookGroup/BookGroup";

const GetBooks = () => {
  const [query, setQuery] = useState("");

  const handleChange = e => {
    setQuery(e.currentTarget.value);
  };

  const handleSubmit = e => {
    e.preventDefault();
    console.log(query);
  };

  return (
    <div className="getbooks">
      <h2>Please input the book title to search</h2>
      <form className="search-bar" onSubmit={handleSubmit}>
        <input type="search" onChange={handleChange} value={query} />
        <button type="submit">Search</button>
      </form>

      <div className="gotten-books">
          <BookGroup />
          <BookGroup />
          <BookGroup />
          <BookGroup />
          <BookGroup />
          <BookGroup />
      </div>
    </div>
  );
};

export default GetBooks;
