import React from 'react';
import { Link } from 'react-router-dom';

const HeroText = () => {
    return (
      <div className="hero-text">
        <h1>Unlimited Access to every book you can think of.</h1>
        <p>All the books you would ever need is just one proud step away.</p>
        <div className="btn">
          <Link to="/register">Get Started</Link>
        </div>
      </div>
    );
}

export default HeroText
